@use "sass:color";
body{
    margin: 0;
}

$deg: 10;
$hue1: hsl($deg,100%,60%);
$hue2: hsl($deg+180,100%,60%);
$hue3: hsl($deg+240,100%,60%);
$bg:white;
$borderRadius: 50px;
.Page{
    display: flex;
    flex-wrap: wrap;
    font-family:Roboto;
}
.Header{
    width: 100%;
    height: 10vh;
    max-height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    
    h1{
        color:transparent;
    }
}
.Header center{
    background: linear-gradient(29deg, $hue2, rgba(0, 0, 0, 0)),    
                 linear-gradient(278deg, $hue1, rgba(0, 0, 0, 0)),
                 linear-gradient(110deg, $hue3, rgba(0, 0, 0, 0));
    -o-background-clip: text;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    color: transparent;
}
.Inputs,.Table{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80vh;
    width: auto;
    min-width: 300px;
}
.Inputs{
    justify-items: center;
    width: 40%;
    flex-grow: 1;
    
    .genderRadio{
        margin: 0.3em;

        input{
            display: none;
        }
        span{
            cursor: pointer;
        }
        input:checked + span{
            font-weight: 800;
        }
        
    }
    .inputs-inner{
        width:200px;
        .ui.input{
            width: 100%;
        }
    }
}


.Table{
    width: calc(60% - 40px);
    display: flex;
    flex-wrap: wrap;
    margin:0 20px;
    align-content: center;
    background-clip: content-box;
    flex-grow: 1;
    height: auto;

    .isFrom{
        width: 100%;
        text-align: center;
        font-size: 14px;
    }
    .percent{
        @extend .isFrom;
        font-weight: 800;
        font-size: 24px;
    }
}

.circle{
    width: 15px;
    height: 15px;
    border-radius: 50%;
    border: 1px solid var(--dot-color);
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 1px;
    margin-bottom: 1px;
}
.circle:before{
    width: 0px;
    height: 0px;
}

.circle.dotted:before{
    border-radius: 50%;
    content: "";
    background-color: var(--dot-color);
    width: 15px;
    height: 15px;
    margin: -1px;
    //animation-name: dot-apear;
    //animation-duration: 0.1s;
    //animation-timing-function: linear;
}
@for $x from 1 through 1200 {
    .circle:nth-child(#{$x}){
        // opacity: (300 / $x);
        // rgb(255 187 51)
        // rgb(51 119 255)
        $dotColor:  mix($hue1,$hue2, ((100/1200)*$x));
        --dot-color: #{$dotColor};
        // background-color: mix($hue1,$hue2, ((100/1200)*$x));
        // --dot-color: rgb(#{256-($x/5)},#{187-($x/11.6)},#{51+($x/5)});
        // --dot-color: rgb(#{51+($x/5)},#{119+($x/11.6)},#{255-($x/5)});
    }
}

