.Footer{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 10vh;
}
.Footer .part{
    width: 30%;
    display: flex;
    justify-content: center;
}
.langBtns{
    text-align: center;
}
.langBtn{
    margin: 0 0.2em;
    height: 37px;
    width: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: $borderRadius;
}
.langBtn:before{
    content: '';
    opacity: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(29deg, $hue2, rgba(0, 0, 0, 0)),    
                 linear-gradient(278deg, $hue1, rgba(0, 0, 0, 0)),
                 linear-gradient(110deg, $hue3, rgba(0, 0, 0, 0));
    border-radius: $borderRadius;
}

.langBtn span{
    width:  100%;
    height: 100%;
    margin-left:-100%;
    display: flex;
    background: $bg;
    border-radius: $borderRadius;
    align-items: center;
    justify-content: center;
    transition: all .2s;
}
.langBtn:hover > span{
    cursor: pointer;
    width: calc(100% - 2px);
    height: calc(100% - 2px);
    margin: 1px 1px;
    margin-left: calc(1px - 100%);
}
.langBtn:hover:before{
    opacity: 1;
}